import React from 'react';
import { Router, Switch, Route } from "react-router-dom";

import './index.css'
import history from './history';

import Header from "./components/Header";
import Main from "./components/MainMenu";

import Commodity from "./components/MasterData/Commodity";
import Variety from "./components/MasterData/Variety";
import AssetGroup from "./components/MasterData/AssetGroup";
import AssetClass from "./components/MasterData/AssetClass";
import AssetStatus from "./components/MasterData/AssetStatus";
import Construction from "./components/MasterData/Construction";
import Direction from "./components/MasterData/Direction";
import SoilType from "./components/MasterData/SoilType";
import UnitMesasurement from "./components/MasterData/UnitMesasurement";
import UnitConverter from "./components/MasterData/UnitConverter";

function App() {
    //const classes = useStyles()
    return (
        <>
        <div>
            <div>
                <Header />
            </div>
                <Router history={history}>
                    <Switch>
                        <Route path="/fourth" exact component={Main} />

                        <Route path="/fourth/Commodity" component={Commodity} />
                        <Route path="/fourth/Variety" component={Variety} />
                        <Route path="/fourth/AssetGroup" component={AssetGroup} />
                        <Route path="/fourth/AssetClass" component={AssetClass} />
                        <Route path="/fourth/AssetStatus" component={AssetStatus} />
                        <Route path="/fourth/Construction" component={Construction} />
                        <Route path="/fourth/Direction" component={Direction} />
                        <Route path="/fourth/SoilType" component={SoilType} />
                        <Route path="/fourth/UnitMesasurement" component={UnitMesasurement} />
                        <Route path="/fourth/UnitConverter" component={UnitConverter} />
                        
                    </Switch>
                </Router>
        </div>
        </>
	);
}

export default App;